'use strict'
import Q from 'q'
import jsapi from './ddApiConfig'
import { logException } from './ravenConfig'
const ddPlugin = {};

var getMethod = function(method, ns) {
    var arr = method.split('.');
    var namespace = ns || dd;
    for (var i = 0, k = arr.length; i < k; i++) {
        if (i === k - 1) {
            return namespace[arr[i]];
        }
        if (typeof namespace[arr[i]] == 'undefined') {
            namespace[arr[i]] = {};
        }
        namespace = namespace[arr[i]];
    }
};

function callJsApi(method, param = {}) {
    return Q.Promise((success, error)=> {

        if (!window.ability || window.ability < jsapi[method]) {
            logException(new Error(' ' + method), {
                method: method,
                param: param
            });
            return error({errCode: 404, msg: '' + method})
        }

        param.onSuccess = function (result) {
            process.env.NODE_ENV !== 'production' && console.log(method, 'success', result)
            success(result)
        };
        param.onFail = function (result) {
            process.env.NODE_ENV !== 'production' && console.log(method, 'fail', result)
            error(result)
            logException(new Error(method+ 'fail'), result);
        };
        getMethod(method)(param);
    })
}


ddPlugin.install = function (Vue, option) {
    var dd = window.dd;

    if(dd){
        Vue.prototype.callJsApi = callJsApi;
        window.callJsApi = callJsApi;
    }else{
        let func = function () {
            console.log('dd');
        };
        Vue.prototype.callJsApi = func;
        window.callJsApi = func;
        console.error('dd');
    }
};


(function (Plugin) {
    if(typeof module === 'object' && typeof module.exports === 'object'){
        module.exports = Plugin;
    }else{
        if(window.bb){
            window.bb['ddPlugin'] = Plugin;
        }else{
            window.bb = {
                ddPlugin:Plugin
            }
        }
    }
})(ddPlugin);



